import SwitchMachineEcharts from './SwitchMachineEcharts.vue';
import xyEcharts from './xyEcharts.vue';
// import xyTable from './xyTable.vue';
import {
  mechanical_properties,
  switch_vibration,
  switch_vibration_chart,
  mechanical_properties_chart,
  devicetype,
  devicename,
  deviceattr,
} from '../../utils/service';
export default {
  created() {

  },
  components: {
    SwitchMachineEcharts,
    xyEcharts,
    // xyTable
  },
  data() {
    return {
      devicename: '', // 设备名称
      devicetype: '', // 设备类型
      deviceattr: '', // 设备属性
      deviceattr2: '',
      timedata: '',
      timedata2: '',
      sdevicename: '', // 确认设备名称
      sdevicetype: '', // 确认设备类型
      sdeviceattr: '', // 确认设备属性
      sdeviceattr2: '', // 确认设备属性2
      pickerOptions: {
        shortcuts: [{
          text: '最近一周',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近一个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近三个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
            picker.$emit('pick', [start, end]);
          }
        }]
      },
      datavalue1: '',
      attribute1: '',
      value2: '',
      value: [],
      dataNameValue: [{
          title: '仪表仓湿度(%)',
          value: 'hummeter'
        }, {
          title: '仪表仓温度(℃)',
          value: 'tempmeter'
        },
        {
          title: '电机动作次数(次)',
          value: 'actiontimes'
        }, {
          title: '电机A相电流动作时间(s)',
          value: 'curactiontimea'
        }, {
          title: '电机B相电流动作时间(s)',
          value: 'curactiontimeb'
        }, {
          title: '电机C相电流动作时间(s)',
          value: 'curactiontimec'
        }, {
          title: 'A相累计电流(A/s)',
          value: 'cumcura'
        }, {
          title: 'B相累计电流(A/s)',
          value: 'cumcurb'
        }, {
          title: 'C相累计电流(A/s)',
          value: 'cumcurc'
        }, {
          title: 'A相平均电流(A)',
          value: 'avecura'
        }, {
          title: 'B相平均电流(A)',
          value: 'avecurb'
        }, {
          title: 'C相平均电流(A)',
          value: 'avecurc'
        }, {
          title: 'A相最小电流(A)',
          value: 'mincura'
        }, {
          title: 'B相最小电流(A)',
          value: 'mincurb'
        }, {
          title: 'C相最小电流(次A)',
          value: 'mincurc'
        }, {
          title: 'A相最大电流(A)',
          value: 'maxcura'
        }, {
          title: 'B相最大电流(A)',
          value: 'maxcurb'
        }, {
          title: 'C相最大电流(A)',
          value: 'maxcurc'
        },
      ],
      datalist: '',

      xydataNameValue: [{
        title: 'X轴频率(Hz)',
        value: 'frequencyx'
      }, {
        title: 'Y轴频率(Hz)',
        value: 'frequencyy'
      }, {
        title: 'Z轴频率(Hz)',
        value: 'frequencyz'
      }, {
        title: 'X轴加速度峰值(m/s²)',
        value: 'accx'
      }, {
        title: 'Y轴加速度峰值(m/s²)',
        value: 'accy'
      }, {
        title: 'Z轴加速度峰值(m/s²)',
        value: 'accz'
      }, {
        title: 'X轴速度有效值(m/s²)',
        value: 'velx'
      }, {
        title: 'Y轴速度有效值(m/s²)',
        value: 'vely'
      }, {
        title: 'Z轴速度有效值(m/s²)',
        value: 'velz'
      }, {
        title: 'X轴位移峰峰值(µm)',
        value: 'disx'
      }, {
        title: 'Y轴位移峰峰值(µm)',
        value: 'disy'
      }, {
        title: 'C相累计电流(µm)',
        value: 'disz'
      }, {
        title: 'X轴倾角(°)',
        value: 'dipanglex'
      }, {
        title: 'Y轴倾角(°)',
        value: 'dipangley'
      }, {
        title: 'Z轴倾角(°)',
        value: 'dipanglez'
      }, {
        title: '温度(℃)',
        value: 'temp'
      }, ],
      xydatalist: '',
      edata: [],
      edata2: [],
      exydata: [],
      exydata2: [],
      mechanical_dvc_id: [],
      mechanical_dvc_id2: [],
      exydataname: '',
      exydata2name: '',
      edata2name: '',
      edataname: '',
    };
  },

  mounted() {
    this.sTblist()
    this.getdeviceattr1()
    this.getdeviceattr2()
  },
  methods: {
    sTblist() {
      mechanical_properties({}, res => {
        this.datalist = res.data
        // var dvc_id=[]
        this.datalist.map(i => {
          this.mechanical_dvc_id.push({
            device_id: i.device_id,
            device_name: i.device_name
          })
        })
        console.log('this.mechanical_dvc_id :>> ', this.datalist);

      });
      switch_vibration({}, res => {
        this.xydatalist = res.data
        this.xydatalist.map(i => {
          this.mechanical_dvc_id2.push({
            device_id: i.device_id,
            device_name: i.device_name
          })
        })

      });
    },
    topsure() {
      this.edatalist = []
      var startdate = ''
      var enddate = ''
      for (let index = 0; index < this.timedata.length; index++) {
        startdate = this.timedata[0];
        enddate = this.timedata[1];
      }
      this.firtlist(startdate, enddate)
      this.towlist(startdate, enddate)
    },
    firtlist(startdate, enddate) {
      var formData = {
        startdate: startdate,
        enddate: enddate,
        attrname: this.sdeviceattr,
        deviceid: this.mechanical_dvc_id[0].device_id,
      }
      mechanical_properties_chart(formData, res => {
        var datalist = res.data
        var listpush = []
        datalist.map(item => {
          listpush.push([item.acquisitiontime, item[this.sdeviceattr]])
        })
        this.edata = listpush
      });
      this.edataname = this.mechanical_dvc_id[0].device_name

    },
    towlist(startdate, enddate) {
      var formData1 = {
        startdate: startdate,
        enddate: enddate,
        attrname: this.sdeviceattr,
        deviceid: this.mechanical_dvc_id[1].device_id,
      }
      mechanical_properties_chart(formData1, res => {
        var datalist = res.data
        var listpush = []
        datalist.map(item => {
          listpush.push([item.acquisitiontime, item[this.sdeviceattr]])
        })
        this.edata2 = listpush
      });
      this.edata2name = this.mechanical_dvc_id[1].device_name
    },
    topsure2() {
      var startdate = ''
      var enddate = ''
      for (let index = 0; index < this.timedata2.length; index++) {
        startdate = this.timedata2[0];
        enddate = this.timedata2[1];
      }
      this.firtlist2(startdate, enddate)
      this.towlist2(startdate, enddate)

    },
    firtlist2(startdate, enddate) {
      var formData = {
        startdate: startdate,
        enddate: enddate,
        attrname: this.sdeviceattr2,
        deviceid: this.mechanical_dvc_id2[0].device_id,
      }
      switch_vibration_chart(formData, res => {
        var datalist = res.data
        var listpush = []
        datalist.map(item => {
          listpush.push([item.acquisitiontime, item[this.sdeviceattr2]])
        })
        this.exydata = listpush
      });
      this.exydataname = this.mechanical_dvc_id2[0].device_name

    },
    towlist2(startdate, enddate) {
      var formData1 = {
        startdate: startdate,
        enddate: enddate,
        attrname: this.sdeviceattr2,
        deviceid: this.mechanical_dvc_id2[1].device_id,
      }
      switch_vibration_chart(formData1, res => {
        var datalist1 = res.data
        var listpush1 = []
        datalist1.map(item => {
          listpush1.push([item.acquisitiontime, item[this.sdeviceattr2]])
        })
        this.exydata2 = listpush1
      });
      this.exydata2name = this.mechanical_dvc_id2[1].device_name
    },
    // 获取设备属性  // 021082机械
    getdeviceattr1() {
      var formData = {
        devicetype: '021082',
      }
      deviceattr(formData, res => {
        this.deviceattr = res.data
      });
    },
    changesdeviceattr(e) {
      this.sdeviceattr = e
    },
    // 获取设备属性  // 021082机械
    getdeviceattr2() {
      var formData = {
        devicetype: '021083',
      }
      deviceattr(formData, res => {
        this.deviceattr2 = res.data
      });
    },
    changesdeviceattr2(e) {
      this.sdeviceattr2 = e
    }
  }
};



