<template>
    <div class="SwitchMachine">
        <div class="bg">
            <div class="title">
                <span></span>
                <div class="title_name">分接开关电机工作特性</div>
            </div>
            <div class="gc">
                <div>
                    <div v-show="datalist.length == 0" class="nodata">无数据 </div>
                    <div class="table" v-show="datalist.length != 0">
                        <table class="list-table">
                            <thead>
                                <tr class="table-title">
                                    <th class="tac"><span class="table_span"></span></th>
                                    <th class="tac" v-for="(item0, i0) in datalist" :key="i0">{{ item0.device_name }}
                                    </th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr class="table-content" v-for="(item, i) in  dataNameValue" :key="i">
                                    <td class="table-type">{{ item.title }}</td>
                                    <td v-for="(item1, i1) in datalist" :key="i1">{{ item1[item.value] }}</td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
                <div style="width: 950px;">
                    <div class="choice">
                        <div class="data">
                            <span>监测时间:</span>
                            <el-date-picker v-model="timedata" type="datetimerange" align="right"
                                start-placeholder="开始日期" value-format="yyyy-MM-dd HH:mm:ss" end-placeholder="结束日期"
                                :default-time="['00:00:00', '23:59:59']" :picker-options="pickerOptions">
                            </el-date-picker>
                        </div>
                        <div>
                            <span>设备属性：</span>
                            <el-select v-model="sdeviceattr" clearable placeholder="请选择">
                                <el-option v-for="item in deviceattr" :key="item.value" :label="item.attr_name"
                                    :value="item.attr_id" @change="changesdeviceattr">
                                </el-option>
                            </el-select>
                        </div>
                        <div>
                            <el-button type="success" plain size="medium" class="button" @click="topsure">确认</el-button>
                        </div>
                    </div>
                    <SwitchMachineEcharts :edata="edata" :edata2="edata2" :edata2name="edata2name"
                        :edataname="edataname" v-if="edata2name || edataname" />
                </div>
            </div>
        </div>
        <div class="bg">
            <div class="title">
                <span></span>
                <div class="title_name">分接开关振动特性</div>
            </div>
            <div class="gc">
                <div>
                    <div v-show="datalist.length == 0" class="nodata">无数据</div>
                    <div class="table" v-show="datalist.length != 0">
                        <table class="list-table">
                            <thead>
                                <tr class="table-title">
                                    <th class="tac"><span class="table_span"></span></th>
                                    <th class="tac" v-for="(item0, i0) in xydatalist" :key="i0">{{ item0.device_name }}
                                    </th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr class="table-content" v-for="(item, i) in  xydataNameValue" :key="i">
                                    <td class="table-type">{{ item.title }}</td>
                                    <td v-for="(item1, i1) in xydatalist" :key="i1">{{ item1[item.value] }}</td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
                <div style="width: 950px;">
                    <div class="choice">
                        <div class="data">
                            <span>监测时间:</span>
                            <el-date-picker v-model="timedata2" type="datetimerange" align="right"
                                start-placeholder="开始日期" value-format="yyyy-MM-dd HH:mm:ss" end-placeholder="结束日期"
                                :default-time="['00:00:00', '23:59:59']" :picker-options="pickerOptions">
                            </el-date-picker>
                        </div>
                        <div>
                            <span>设备属性：</span>
                            <el-select v-model="sdeviceattr2" clearable placeholder="请选择">
                                <el-option v-for="item in deviceattr2" :key="item.value" :label="item.attr_name"
                                    :value="item.attr_id">
                                </el-option>
                            </el-select>
                        </div>
                        <div>
                            <el-button type="success" plain size="medium" class="button" @click="topsure2">确认
                            </el-button>
                        </div>
                    </div>
                    <xyEcharts :exydata='exydata' :exydata2='exydata2' :exydata2name="exydata2name"
                        :exydataname="exydataname" v-if="exydataname || exydata2name" />
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import SwitchMachine from "./SwitchMachine";
export default SwitchMachine;
</script>
<style scoped>
@import url("SwitchMachine.css");
@import url("../../assets/css/common.css");
</style>


